form{
    width: 90%;
    max-width: 550px;
    height: 300px;
    background-color: #e1918e;
    padding: 15px 15px;
    display: flex;
    justify-content: center;
    align-items: center;
    flex-direction: column;
    border-radius: 25px;
}

label{
    text-align: center;
    font-size: 40px;
    font-weight: bold;
    color: #f4f0e5;
    font-family: 'Lobster', cursive;
    margin-bottom: 5px;
}

textarea{
    background-color: #ed707e;
    border: 2px solid #e15e6e;
    border-radius: 20px;
    font-weight: bold;
    color: #f4f0e5;
    font-size: 30px;
    text-align: center;
    padding: 5px;
    height: 200px;
    width: calc(100% - 15px);
}

textarea::placeholder{
    color: #e0c4ae;
    font-family: 'Montserrat';
    font-weight: 400;
    font-weight: bold;
    font-size: 30px;
}

textarea:focus{
    outline-color: #c3e6d4;
}

.TodoForm-buttonContainer{
    margin-top: 10px;
    display: flex;
    justify-content: space-between;
    align-items: center;
    width: 100%;
}

.TodoForm-button{
    cursor: pointer;
    display: inline-block;
    font-size: 30px;
    color: #202329;
    font-weight: 400;
    width: 120px;
    height: 50px;
    border-radius: 10px;
    border:none;
    font-family: 'Montserrat';
}

.TodoForm-button--add{
    background-color: #e15e6e;
    border: 2px solid #e15e6e;
    color: #e1918e;
    font-weight: bold;
}

.TodoForm-button--add:hover{
    background-color: #6aea6a;
    border: none;
    color: #202329;
    box-shadow: 0px 0px 15px rgba(97, 250, 97, 0.9);
}

.TodoForm-button--cancel{
    background-color: #e15e6e;
    border: 2px solid #e15e6e;
    color: #e1918e;
    font-weight: bold;
}

.TodoForm-button--cancel:hover{
    background-color: #ff261b;
    border: none;
    color: #202329;
    box-shadow: 0px 0px 15px rgba(255, 38, 27, 0.9);
}

@media (max-width: 768px) {
    form{
        max-width: 320px;
    }

    label{
        font-size: 30px;
    }

    textarea{
        font-size: 20px;
        height: 170px;
    }

    textarea::placeholder{
        font-size: 20px;
    }

    .TodoForm-button{
        font-size: 20px;
        width: calc(45% - 15px);
    }

    .TodoForm-button--add{
        background-color: #74f574;
        border: none;
        color: #202329;
        box-shadow: 0px 0px 15px rgba(97, 250, 97, 0.9);
    }
    
    .TodoForm-button--cancel{
        background-color: #fe3d33;
        border: none;
        color: #202329;
        box-shadow: 0px 0px 15px rgba(255, 38, 27, 0.9);
    }
}