.loading{
    padding: 0;
    margin-bottom: 5px;
    margin-top: 5px;
    width: 500px;
}

.loading li{
    list-style: none;
    display: flex;
    justify-content: space-between;
    align-items: center;
    background-color: #e1918e;
    border-radius: 35px;
    color: #f4f0e5;
    margin-top: 10px;
    height: 60px;
}

.first_circle,
.second_circle{
    width: 50px;
    height: 50px;
    border-radius: 50%;
}

.first_circle{
    margin-left: 5px;
    background-color: #f2bcbb;
    animation: first 1.5s;
    animation-iteration-count: 10;
}

.second_circle{
    margin-right: 5px;
    background-color: #f2bcbb;
    animation: second 1.5s;
    animation-iteration-count: 10;
}

@keyframes first{
    50%{
        transform: translateX(438px);
    }
}

@keyframes second{
    50%{
        transform: translateX(-438px);
    }
}

@media (max-width: 768px){
    .loading{
        width: 300px;
    }

    .loading li{
        border-radius: 35px;
        height: 50px;
    }

    .first_circle,
    .second_circle{
        width: 45px;
        height: 45px;
    }

    @keyframes first{
        50%{
            transform: translateX(245px);
        }
    }
    
    @keyframes second{
        50%{
            transform: translateX(-245px);
        }
    }
}
